@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
*{
  font-family: 'Roboto', sans-serif;
  font-size: 1.2rem;
}

body{
  display: flex;
  justify-content: center;
  align-items: center;
  background: #262834;
  
}

.container{
  overflow: hidden;
  top: 50%;
  left: 50%;
  position: absolute;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0  auto;
  width: 85%;
  height: 70%;  
  text-align: center;
  border: 1.2rem solid #101116;
  background: #101116;
  border-radius: 0.4rem;
}

.keypad{
  display: grid;
  grid-template-columns: repeat(4,1fr);
  grid-auto-rows: minmax(3.75rem, auto);
}

input[type="text"]{
  outline: none;
  height: 3rem;
  width: 100%;
  background-color:#10111600;
  border: 0rem;
  color: #ffffff;
  text-align: right;
  font-size: 1.8rem;
  font-weight: 500;
  letter-spacing: 0.2rem;
}

#clear{
  grid-column: 1/3;
  grid-row: 1;
  color: #262834;
}

#result {
  grid-column: 3/5;
  grid-row: 5;
}

#backspace{
  color: #262834;
}

button{
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: #262834;
  color: #ffffff;
  font-weight: 500;
}

input:focus,
input:active,
button:focus,
button:active{
  outline:none ;
 
}

.highlight {
  background: #56cbdb;
}


@media(min-width: 600px){
 .container{
   width: 250px;
   height: auto;
 }
}



